/* Please create another file for fonts */
@font-face {
    font-family: Fredoka_One;
    src: url('./fonts/FredokaOne-Regular.ttf');
}

@font-face {
    font-family: TitanOne-Regular;
    src: url('./fonts/TitanOne-Regular.ttf');
}

@font-face {
  font-family: Ubuntu-Regular;
  src: url('./fonts/ubuntu/Ubuntu-Regular.ttf');
}

@font-face {
  font-family: Ubuntu-Bold;
  src: url('./fonts/ubuntu/Ubuntu-Bold.ttf');
}

@font-face {
  font-family: DejavuSans;
  src: url('./fonts/DejaVuSans.ttf');
}


/* utility classes */

.w--full {
    width: 100%;
}
.h--full {
    height: 100vh;
}
.d--inline-block {
    display: inline-block;
}
.d--block {
    display: block;
}

.d--flex {
  display: -ms-flexbox;
  display: flex;
}

.f--left {
    float: left;
}
.f--right {
    float: right;
}
.clear {
    clear: both;
    content: '';
}
.p--0 {
    padding: 0;
}
.m--0 {
    margin: 0;
}
.l-style--none {
    list-style: none;
}
.text-d--none {
    text-decoration: none;
}
.text-t--up {
    text-transform: uppercase;
}

.text-a--center {
  text-align: center;
}

.c--white {
  color: white;
}

.f-f--ourF {
  font-family: Fredoka_one;
}

.b--none {
  border: none;
}

.b--white {
  border: 0.4vmin solid white;
}

.b--black {
  border: 0.4vmin solid black;
}

.oln--none {
  outline: none;
}

.pst--abs {
  position: absolute;
}

.bg--gradient {
background: -webkit-gradient(linear, left top, right top, from(#2dbf91), to(#55a6a1));
background: -webkit-linear-gradient(left, #2dbf91, #55a6a1);
background: -o-linear-gradient(left, #2dbf91, #55a6a1);
background: linear-gradient(left, #2dbf91, #55a6a1);

}



